}
.nav_second_side ul{
  list-style: none;
  float:left;
  margin:10px 0px 0px 0px;
}
.nav_second_side ul li a{
  text-align: center;
  margin-right:10px;
  cursor: pointer;
}
.bodypart{
    padding: 0px;
}
