}
.each-slide > div {
  display: flex;
  align-items: center;
  justify-content: center;
  background-size: cover;
  height: 500px;
}

.each-slide span {
  padding: 20px;
  font-size: 20px;
  background: #efefef;
  text-align: center;
}
.notification_box{
  border: 1px solid;
  width: 20%;
}
.notification_outer_box{
  border: 1px solid;
  display: flex;
}
